<template>
<div class="pxy-login">
    <div class="pxy-login-title">
      <p>平行云境</p>
      <p>元宇宙活动平台</p>
    </div>
    <div class="pxy-login-form">
      <p>登录</p>
      <el-form
        ref="ruleFormRef"
        :model="bindForm"
        :rules="rules"
        label-position="top"
      >
        <el-form-item label="用户" prop="phone">
          <el-input class="phone-input" v-model="bindForm.phone" type="text" autocomplete="off" placeholder="请输入手机号" @blur="getActivityAuth" />
        </el-form-item>
        <el-form-item label="验证码" prop="code">
          <el-row type="flex" justify="space-between">
              <el-input v-model="bindForm.code" placeholder="请输入验证码" :clearable="true" class="code-input" />
              <el-button 
                :disabled="!(captchaTime===60) || !allowPermission" 
                plain 
                @click="getCaptchaCodeFn" 
                style="color: #32cdcd;border: 1px solid #32cdcd;"
              >{{ captchaText }}</el-button>
            </el-row>
            <span v-if="!allowPermission" class="primary-color" style="font-size: 14px;margin-top: 13px;display: inline--block;">您尚未报名或未通过审核，无法进入会议，请联系主办方</span>
        </el-form-item>
        <el-form-item>
          <el-button :disabled="!allowPermission" size="large" class="login-btn" type="primary" @click.prevent="loginFn">
            立即登录
          </el-button>
        </el-form-item>
        <el-form-item prop="agreeValue" style="text-align:center;">
          <el-checkbox v-model="bindForm.agreeValue">已阅读并同意<span @click="toAgreement">《平行云元宇宙大会用户协议》</span></el-checkbox>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import { apiGetCaptcha, apiPostLogin, postMemberId,apiPostLoginBysms, apiPostApplyStatus, apiGetSms, apiGetAuthEnter } from '@/apis/api'

const validatePhone = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请输入手机号'))
  } else if (!/^1[3456789]\d{9}$/.test(value)) {
    callback(new Error('请输入正确的手机号'))
  } else {
    callback()
  }
}
const validateAgree = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请阅读勾选该用户协议'))
  } else {
    callback()
  }
}
export default {
  data() {
    return {
      ruleForm: {
        username: '',
        password: '',
        captchaValue: '',
        captchaId: '', // 验证码ID
        agreeValue: false
      },
      rules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        captchaValue: [{ required: true, message: '请输入验证码', trigger: 'blur' }],
        agreeValue: [{ required: true, message: '请阅读勾选该用户协议', trigger: 'blur change' },{ validator: validateAgree, trigger: 'blur' }],
        code: [{ required: true, message: '请输入验证码', trigger: 'blur' }],
        phone: [{ required: true, message: '请输入手机号', trigger: 'blur' }, { validator: validatePhone, trigger: 'blur' }],

      },
      captchaUrl: '',
      bindForm: {
        phone: '',
        code: '',
        agreeValue: true
      },
      captchaTime: 60,
      captchaText: '获取验证码',
      allowPermission: true, // 是否有权限进入应用
    }
  },
  methods: {
    // 判断是否有活动权限
    getActivityAuth() {
      if(/^1[3456789]\d{9}$/.test(this.bindForm.phone)) {
        const params = {
          phone: this.bindForm.phone,
          code: this.$route.query.code || localStorage.getItem('activityCode')
        }
        apiGetAuthEnter(params).then(res => {
          if(res && res.code === 1000) {
            this.allowPermission = true;
          }else if(res && res.code === 430) {
            this.allowPermission = false;
          }
        })
      }
    },
    toAgreement() {
      this.$router.push('/agreement')
    },
    // 登录
    loginFn () {
      if(!this.allowPermission)return;
      const params = {
        ...this.bindForm,
        activityCode: this.$route.query.code || localStorage.getItem('activityCode')
      }
      this.$refs.ruleFormRef.validate((valid) => {
        if (valid) {
          apiPostLoginBysms(params).then((res) => {
            if (res.code === 1000) {
              const data = res.result;
              if(data.userTitle==='管理员') {
                // 是管理员角色
                this.$store.commit('setIsAdminAuth', true);
                window.localStorage.setItem('isAdminAuth', true);
              }
              window.localStorage.setItem('userInfo', JSON.stringify(data));
              this.$store.commit('setUserInfo', JSON.stringify(data));
              this.getMemberId();//获取memberId
              this.$router.push(`/appli/enter`)
            } else {
              this.$message.error(res.message)
            }
          })
        }
      })
    },
    // 获取验证码
    getCaptchaCodeFn () {
      if(!this.bindForm.phone)  {
        this.$message.error('请输入手机号');
        return
      }
      if(!/^1[3456789]\d{9}$/.test(this.bindForm.phone))  {
        this.$message.error('请输入正确的手机号');
        return
      }
      apiGetSms({phone: this.bindForm.phone}).then(res => {
        if(res && res.code === 1000) {
          this.$message.success('验证码发送成功')
          this.captchaTime--
          this.captchaText = this.captchaTime + 'S后重新发送'
          const timer = setInterval(() => {
          this.captchaTime--
          this.captchaText = this.captchaTime + 'S后重新发送'
          if (this.captchaTime === 0) {
            clearInterval(timer)
            this.captchaText = '重新发送验证码'
            this.captchaTime = 60
          }
          }, 1000)
        } else {
          this.$message.error(res.message)
        }
      })
      
    },
    // 获取memberId
    async getMemberId() {
      const params = {
        activityId : this.$store.state.activityId,
      }
      await postMemberId(params).then(res => {
        if(res.code === 1000) {
          this.$store.commit('setMemberId', res.result);
          window.localStorage.setItem('memberId', res.result);
        } else {
          this.$message.error(res.message)
        }
      })
    },
  },
  mounted() {
    const submitForms=this.loginFn
    document.onkeyup = function(e) {
      const key = e.key;
      if (key === 'Enter') {
        submitForms()
      }
    }
    if(this.$route.query.activityId) {
      // 存储活动ID
      this.$store.commit('setActivityId', this.$route.query.activityId);
      window.localStorage.setItem('activityId', this.$route.query.activityId);
    }
    if(this.$route.query.code) {
      // 存储活动code码，防止接口400跳转到login的时候code码丢失
      window.localStorage.setItem('activityCode', this.$route.query.code);
    }
    // 防止页面不是正常退出时，其他角色有管理员权限
    if(localStorage.getItem('isAdminAuth')) {
      this.$store.commit('setIsAdminAuth', false)
      localStorage.removeItem('isAdminAuth')
    }
  },
  destroyed(){
    document.onkeyup = null;
  }
}
</script>
<style lang="scss" scoped>
.pxy-login {
  width: 100%;
  height: 100%;
  background: url('~@/assets/images/login-bg.png') no-repeat;
  background-size: 100% 100%;
  position: relative;
  .login-btn {
    width: 100%;
    height: 45px;
    margin-top: 30px;
  }
  .phone-input {
    width:100%;
    height: 45px;
    border-radius:20px;
  }
  .code-input {
    width:200px;
  }
  /deep/.el-input__inner {
    border-radius: 10px!important;
  }
  /deep/.el-form-item__label {
    line-height: 14px!important;
    margin-top:14px!important;
  }
  /deep/.el-form-item  {
    margin-bottom: 10px!important;
  }
  &-title{
    position: absolute;
    left: 15%;
    top: 13%;
    text-align: center;
    p {
      font-size: 64px;
      color: #32CDCD;
      font-weight: 600;
      line-height: 85px;
    }
  }
  &-form {
    width: 457px;
    height: 613px;
    background-color: #fff;
    position: absolute;
    right: 7.8%;
    top: 18%;
    border-radius: 23px;
    padding: 100px 50px 0 50px;
    >p:nth-of-type(1) {
      font-size: 30px;
      font-weight: 600;
      margin-bottom: 15px;
    }
    .pxy-form-title {
      font-size: 14px;
      line-height: 15px;
      display: block;
    }
  }
}

@media (max-width: 740px) {
  /deep/.el-input__inner {
    height: 60px;
  }
  .pxy-login {
    background: url('~@/assets/images/mob-login-bg.png') no-repeat;
    background-size: 100% 100%;
    &-title{
    position: absolute;
    left: auto;
    width: 100%;
    text-align: center;
    top: 3%;
    text-align: center;
    p {
      font-size: 54px;
      color: #32CDCD;
      font-weight: 600;
      line-height: 75px;
    }
  }
  }
  .pxy-login-form {
    width: 90%;
    height: 50%;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 5%;
    position: absolute;
    right: 5%;
    top: auto;
    bottom: 100px;
    border-radius: 23px;
    padding: 100px 50px 0 50px;
    >p:nth-of-type(1) {
      font-size: 30px;
      font-weight: 600;
      margin-bottom: 15px;
    }
    .pxy-form-title {
      font-size: 14px;
      line-height: 15px;
      display: block;
    }
    .login-btn {
      width: 100%;
      height: 60px;
      margin-top: 30px;
    }
    .phone-input {
      width:100%;
      height: 60px;
      border-radius:20px;
    }
    .code-input {
      width:60%;
    }
    /deep/.el-form-item__label {
      line-height: 25px!important;
      margin-top:25px!important;
    }
    /deep/.el-form-item  {
      margin-bottom: 25px!important;
    }
    /deep/.el-checkbox__inner {
      width: 1.5rem;
      height: 1.5rem;
    }
    /deep/.el-checkbox__inner::after{
      width: 0.5rem;
      height: 0.8rem;
    }
  }
  
}
</style>